define(["react", "reactDOM", "./datatablerow"],
function (React, ReactDOM, DataTableRow)
{
    return class DataTable extends React.Component
    {
        constructor()
        {
            super();
        }

        render()
        {
            //const recordList = this.props.recordList;
            //const headerList = this.props.headerList;

            const recordList =
            [
                { id: 1, firstName: "Mark", lastName: "Otto", userName: "@mdo" },
                { id: 2, firstName: "Jacob", lastName: "Thornton", userName: "@fat" },
                { id: 3, firstName: "Larry", lastName: "the Bird", userName: "@twitter" }
            ];

            const headerList = ["Id", "First Name", "Last Name", "Username"];

            return (
                <table className="table">
                    <thead>
                        <tr>
                            {headerList.map((header) =>
                                <th>{header}</th>
                            )}
                        </tr>
                    </thead>
                    <DataTableRow recordList={recordList} />
                </table>
            );
        }
    }
});